import React, { Component } from 'react';
import { render } from 'react-dom';
import _ from 'lodash';

import OecdTable from 'oecd-table';
import data from './mock-data.json';

import './assets/theme-default.less';
import './assets/theme-default.css';

class App extends Component {
  state = { data };

  componentDidMount() {
    this.oecdMasterTable.on('SORT_DATA', sortState => this.oecdSlaveTable.sort(sortState));
  }

  changeTableDataHandler = (event) => {
    event.preventDefault();
    this.setState({ data: _.isEmpty(this.state.data) ? data : {}});
  };

  render() {
    const isDataEmpty = _.isEmpty(this.state.data);

    return (
      <div className="dotstat">
        <h1>dotstat-client</h1>
        <a href="#" onClick={ this.changeTableDataHandler }>
          { isDataEmpty ? 'set data' : 'unset data' }
        </a>
        <hr />
        <div className="items">
          <div className="item">
            <h2>table (master)</h2>
            <OecdTable data={ this.state.data } ref={ c => this.oecdMasterTable = c } />
          </div>
          <div className="item">
            <h2>table (slave)</h2>
            <OecdTable data={ this.state.data } ref={ c => this.oecdSlaveTable = c }  />
          </div>
        </div>
      </div>
    );
  }
};

export default App;

render(<App />, document.getElementById('root'));



